$normal-text-color: #444444;
$nav-link-color: #333;
$nav-link-hover-color: black;
$link-color: #47a;
$visited-link-color: #941452;

$last-p-padd: 1.5em;
$nav-padd: 1em;
$line-under: 1px solid #aaa; 

$line-height: 1.5em;
$term-line-height: 1em;
$file-line-height: 1.2em;

body {
 background-color: #fefefe;
 padding: 16px;
 font-family: -apple-system, helvetica, arial, sans-serif;
 font-size: 16px;
 color: $normal-text-color;
 line-height: $line-height;
 overflow-wrap: break-word;
}
#wrapper {
 max-width: 800px;
 margin: auto;
 > header {
 text-align: center;
 }
}

.clear-list {
 list-style-type: none;
 margin: 0;
 padding: 0;
}
.clear-list li {
 margin: 0;
 padding: 0;
}
.projects a {
 font-weight: bold;
}
h1, h2, h3, h4, h5, h6 {
 line-height: 1em;
}
header {
 margin-bottom: 32px;
}
hr {
 border: none;
 border-bottom: 1px solid #999;
}
a {
 text-decoration: underline;
 color: $link-color;
} 
a:visited {
 color: $visited-link-color;
}
a.nav-link,
a.post-title-link {
 color: $nav-link-color;
 text-decoration: none;
}
.post-title {
 line-height: $line-height;
 color: $nav-link-color;
}
a.citation-link {
 text-decoration: none;
}

nav {
 text-align: center;
 padding: $nav-padd 0px;
}
nav a {
 margin: 1em;
 color: $nav-link-color;
 font-weight: bold;
 font-style: none;
}
nav a:hover {
 text-decoration: underline; 
}

li {
 margin: .5em;
}

p {
 padding: .1em 0;
}

table,
table tr,
table td,
table th {
 border: 1px solid #aaa;
 border-collapse: collapse;
 padding: 5px;
 font-weight: normal;
}
table th {
 font-weight: bold;
}
table {
 width: 75%;
 margin: auto;
}

img {
 display: block;
 width: 55%;
 margin-left: auto;
 margin-right: auto;
}

blockquote {
 font-style: italic;
}
address { 
 font-style: normal;
}

@media screen and (max-width: 600px){
 body {
 width: 90%;
 }
 #info {
 margin: 0 7px;
 }
 .menu-content {
 max-height: 0;
 overflow: hidden;
 }
 nav {
 text-align: left;
 width: 100%;
 }
 nav a {
 display: block;
 text-align: left;
 padding-left: 0;
 margin-left: 0;
 }
}

.mono {
 font-family: monospace;
}

.bold {
 font-weight: bold;
}

sup {
 margin: 0;
 padding: 0;
}

figcaption {
 margin-top: 10px;
 font-style: italic;
}

footer {
 padding-top: 16px;
 margin-bottom: 100px;
}

.terminal {
 line-height: $term-line-height;
 overflow: scroll;
 padding: 10px; 
 color: #00FF41;
 margin: 0px;
 background-color: #151515;
}

.file {
 overflow: scroll;
 padding: 10px;
 margin: 0px;
 line-height: $file-line-height;
 background-color: #dfdfdf;
 color: #000;
}

.small-image {
 width: 100%;
}

.post-date {
 font-size: 18px;
 text-transform: uppercase;
 font-weight: bold;
 color: #555;
}

.post-excerpt {
 margin: 16px;
 margin-bottom: 0;
}


/* code highlghting */
.highlight .hll { background-color: #ffffcc }
.highlight { background: #f0f0f0; }
.highlight .c { color: #60a0b0; font-style: italic } /* Comment */
.highlight .err { border: 1px solid #FF0000 } /* Error */
.highlight .k { color: #007020; font-weight: bold } /* Keyword */
.highlight .o { color: #666666 } /* Operator */
.highlight .ch { color: #60a0b0; font-style: italic } /* Comment.Hashbang */
.highlight .cm { color: #60a0b0; font-style: italic } /* Comment.Multiline */
.highlight .cp { color: #007020 } /* Comment.Preproc */
.highlight .cpf { color: #60a0b0; font-style: italic } /* Comment.PreprocFile */
.highlight .c1 { color: #60a0b0; font-style: italic } /* Comment.Single */
.highlight .cs { color: #60a0b0; background-color: #fff0f0 } /* Comment.Special */
.highlight .gd { color: #A00000 } /* Generic.Deleted */
.highlight .ge { font-style: italic } /* Generic.Emph */
.highlight .gr { color: #FF0000 } /* Generic.Error */
.highlight .gh { color: #000080; font-weight: bold } /* Generic.Heading */
.highlight .gi { color: #00A000 } /* Generic.Inserted */
.highlight .go { color: #888888 } /* Generic.Output */
.highlight .gp { color: #c65d09; font-weight: bold } /* Generic.Prompt */
.highlight .gs { font-weight: bold } /* Generic.Strong */
.highlight .gu { color: #800080; font-weight: bold } /* Generic.Subheading */
.highlight .gt { color: #0044DD } /* Generic.Traceback */
.highlight .kc { color: #007020; font-weight: bold } /* Keyword.Constant */
.highlight .kd { color: #007020; font-weight: bold } /* Keyword.Declaration */
.highlight .kn { color: #007020; font-weight: bold } /* Keyword.Namespace */
.highlight .kp { color: #007020 } /* Keyword.Pseudo */
.highlight .kr { color: #007020; font-weight: bold } /* Keyword.Reserved */
.highlight .kt { color: #902000 } /* Keyword.Type */
.highlight .m { color: #40a070 } /* Literal.Number */
.highlight .s { color: #4070a0 } /* Literal.String */
.highlight .na { color: #4070a0 } /* Name.Attribute */
.highlight .nb { color: #007020 } /* Name.Builtin */
.highlight .nc { color: #0e84b5; font-weight: bold } /* Name.Class */
.highlight .no { color: #60add5 } /* Name.Constant */
.highlight .nd { color: #555555; font-weight: bold } /* Name.Decorator */
.highlight .ni { color: #d55537; font-weight: bold } /* Name.Entity */
.highlight .ne { color: #007020 } /* Name.Exception */
.highlight .nf { color: #06287e } /* Name.Function */
.highlight .nl { color: #002070; font-weight: bold } /* Name.Label */
.highlight .nn { color: #0e84b5; font-weight: bold } /* Name.Namespace */
.highlight .nt { color: #062873; font-weight: bold } /* Name.Tag */
.highlight .nv { color: #bb60d5 } /* Name.Variable */
.highlight .ow { color: #007020; font-weight: bold } /* Operator.Word */
.highlight .w { color: #bbbbbb } /* Text.Whitespace */
.highlight .mb { color: #40a070 } /* Literal.Number.Bin */
.highlight .mf { color: #40a070 } /* Literal.Number.Float */
.highlight .mh { color: #40a070 } /* Literal.Number.Hex */
.highlight .mi { color: #40a070 } /* Literal.Number.Integer */
.highlight .mo { color: #40a070 } /* Literal.Number.Oct */
.highlight .sa { color: #4070a0 } /* Literal.String.Affix */
.highlight .sb { color: #4070a0 } /* Literal.String.Backtick */
.highlight .sc { color: #4070a0 } /* Literal.String.Char */
.highlight .dl { color: #4070a0 } /* Literal.String.Delimiter */
.highlight .sd { color: #4070a0; font-style: italic } /* Literal.String.Doc */
.highlight .s2 { color: #4070a0 } /* Literal.String.Double */
.highlight .se { color: #4070a0; font-weight: bold } /* Literal.String.Escape */
.highlight .sh { color: #4070a0 } /* Literal.String.Heredoc */
.highlight .si { color: #70a0d0; font-style: italic } /* Literal.String.Interpol */
.highlight .sx { color: #c65d09 } /* Literal.String.Other */
.highlight .sr { color: #235388 } /* Literal.String.Regex */
.highlight .s1 { color: #4070a0 } /* Literal.String.Single */
.highlight .ss { color: #517918 } /* Literal.String.Symbol */
.highlight .bp { color: #007020 } /* Name.Builtin.Pseudo */
.highlight .fm { color: #06287e } /* Name.Function.Magic */
.highlight .vc { color: #bb60d5 } /* Name.Variable.Class */
.highlight .vg { color: #bb60d5 } /* Name.Variable.Global */
.highlight .vi { color: #bb60d5 } /* Name.Variable.Instance */
.highlight .vm { color: #bb60d5 } /* Name.Variable.Magic */
.highlight .il { color: #40a070 } /* Literal.Number.Integer.Long */

/* ADDED CUSTOM */
.highlight .n { color: #000; } /* General name */

div.highlight {
 padding: 0px 5px;
 width: 85%;
 margin: auto;
 overflow-x: scroll;
}
